<template>
	<view>
		<button type="default" open-type="getPhoneNumber" @getphonenumber="decryptPhoneNumber">获取手机号</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				
			}
		},
		methods: {
			getPhoneNumber(e) {
			  if (!e.target.iv) {
			    uni.showModal({
			      content: '获取手机号失败！',
			      showCancel: false
			    })
			    return;
			  }
			  uni.checkSession({
			    success: _ => {
			      bindMobileApi({
			        ivdata: e.target.iv,
			        encrypdata: e.target.encryptedData,
			      }).then(res => {
			        this.$emit('callback',true);
			        store.dispatch('getInfo', false)
			        uni.showModal({
			          content: '登录成功',
			          showCancel: false
			        })
			      }).catch(err => {
			        store.dispatch('getInfo', false)
			      })
			    },
			    fail: res => {
			      store.dispatch('getInfo', false)
			      uni.showModal({
			        content: '获取手机号失败，请再次尝试!',
			        showCancel: false
			      })
			    }
			  })
			}
		}
	}
</script>

